import * as actionType from './actionType.js';

export default (state = {
    menu: 0,
    notes: []
}, action) => {
    switch (action.type) {
        case actionType.SYNDATA_UPDATE_MENU: {
            return {...state, menu: action.status};
        }
        case actionType.SYNDATA_UPDATE_NOTE: {
            const{notes=[]} = state;
            notes.push(action.value)
            return {...state, notes: notes};
        }
        default: {
            return state;
        }
    }
};

