import React, { Component } from 'react';
import {
    BrowserRouter as Router,
    Route,
    Switch
} from 'react-router-dom';
import Container from './container';

class App extends Component {
  render() {
    return (
        <Router >
            <div className='app'>
                < Container.Header.view />
                <div className='container'>
                    <Switch>
                        <Route path="/register" component={Container.Register.view} />
                        <Route path="/login" component={Container.Login.view} />
                        <Route path="/sysdata" component={Container.SynData.view} />
                        <Route path="/stackapp" component={Container.Stackapp.view} />
                        <Route path="/" component={Container.Notes.view} />
                    </Switch>
                </div>
            </div>
        </Router>
    );
  }
}

export default App;

