import React, { Component } from 'react';
import {connect} from "react-redux";
import * as actions from "../actions";
import localForage from "localforage";

class SynData extends Component {
    componentDidMount() {

    }

    sys = () => {
        localForage.getItem("menu").then((menu)=>this.props.sysMenu(menu))
        localForage.keys().then((keys) => {
            for(let i = 0; i < keys.length; i++) {
                localForage.getItem(keys[i]).then((value)=>this.props.sysNote(keys[i],value))
            }
        }).catch(function(err) {
            console.log(err);
        });
    }
    render() {
        return (
            <div className="syndata">
                <ul>
                    {
                        this.props.menu === 2 ? <li>菜单同步完毕</li> : ""
                    }
                    {
                        this.props.notes.map(item=>(<li>item</li>))
                    }
                </ul>
                <button className="btn btn-success" onClick={this.sys}>同步</button>
            </div>
        );
  }
}

const mapStateToProps = (state) => {
    return {
        menu: state.sysData.menu,
        notes: state.sysData.notes
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        sysMenu: (menu) => {
            dispatch(actions.sysMenu(menu));
        },
        sysNote: (key,value) => {
            dispatch(actions.sysNote(key,value));
        }
    }
};

export default connect(mapStateToProps,mapDispatchToProps)(SynData);

