import * as actionType from './actionType.js'

export const loginSuccess = () => ({type: actionType.STACKAPP_SUCCESS})

export const getAllUsers = () => {
    return (dispatch, getState) => {
        const url = "https://api.stackexchange.com/2.2/users?key=RAA6KJkHlTYXSdD9tcE4Kw((&site=stackoverflow&page=1&pagesize=10&order=desc&sort=reputation";
        return fetch(url, {
            method: 'GET',//方法大写
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            mode: "cors"
        }).then((response) => {
            //后台返回数据
            if (response.status !== 200) {
                throw new Error('Fail to get response with status ' + response.status);
            }

            response.json().then((responseJson) => {
                alert(JSON.stringify(responseJson))
                // dispatch(loginSuccess(responseJson));
                // dispatch(getUser(responseJson.id_token))
            }).catch((error) => {
                // dispatch(loginFailed());
            });
        }).catch((error) => {
            // dispatch(loginFailed());
        })
    };
}




