import localForage from "localforage";
import * as actionType from './actionType.js'
import * as config from '../config.js'

export const loginSuccess = () =>({type: actionType.SYNDATA_SUCCESS})

export const sysMenu = (menu) => {
    // return {type:actionType.SYNDATA_UPDATE_MENU, status:1}
    // const menu = localForage.getItem("menu")
    // alert("menu:"+JSON.stringify(menu))
    if(menu && menu.menuDirty) {
        return (dispatch, getState) => {
            const url = config.API_PATH+"/api/note-menus";
            return fetch(url, {
                method: 'POST',//方法大写
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer '+ localStorage.getItem("token")
                },
                mode: "cors",
                body: JSON.stringify({content:JSON.stringify(menu.menu)})
            }).then((response) => {
                //后台返回数据
                if (response.status !== 200 && response.status !== 201) {
                    throw new Error('Fail to get response with status ' + response.status);
                }

                menu.menuDirty = false;
                localForage.setItem("menu",menu).then(()=>dispatch({type:actionType.SYNDATA_UPDATE_MENU, status:2}))
            }).catch((error) => {
                // dispatch(loginFailed());
            })
        };
    } else {
        return {type:actionType.SYNDATA_UPDATE_MENU, status:2}
    }
}

export const sysNote = (key,item) => {
    if(item && item.dirty && item.type !== "NOTE") {
        return (dispatch, getState) => {
            const url = config.API_PATH+"/api/notes";
            return fetch(url, {
                method: 'POST',//方法大写
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer '+ localStorage.getItem("token")
                },
                mode: "cors",
                body: JSON.stringify({uuid:item.uuid,md5:item.md5,md:item.md,permiss:item.permiss})
            }).then((response) => {
                //后台返回数据
                if (response.status !== 200 && response.status !== 201) {
                    throw new Error('Fail to get response with status ' + response.status);
                }

                item.dirty = false;
                localForage.setItem(key,item).then(()=>dispatch({type:actionType.SYNDATA_UPDATE_NOTE, value:key}))

            }).catch((error) => {
                // dispatch(loginFailed());
                console.log(error)
            })
        };
    } else {
        return {type:""};
    }

}



